'use client';

import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { useState } from 'react';
import {
    LayoutDashboard,
    Users,
    CreditCard,
    Settings,
    LogOut,
    BarChart3,
    ChevronLeft,
    ChevronRight,
    Sparkles,
    Zap,
    Activity,
} from 'lucide-react';
import {
    Navbar,
    NavbarContent,
    NavbarMenu,
    NavbarMenuItem,
    NavbarMenuToggle,
} from '@heroui/navbar';
import { Button } from '@heroui/button';
import { Avatar } from '@heroui/avatar';
import { Badge } from '@heroui/badge';
import { Divider } from '@heroui/divider';
import { cn } from '@/lib/utils';
import { ThemeSwitch } from '@/components/ThemeSwitch';

const navItems = [
    { href: '/dashboard', icon: LayoutDashboard, label: 'Dashboard', badge: '3' },
    { href: '/providers', icon: Users, label: 'Providers', badge: '4' },
    { href: '/billing', icon: CreditCard, label: 'Billing', badge: '7' },
];

export function Sidebar() {
    const pathname = usePathname();
    const [isMenuOpen, setIsMenuOpen] = useState(false);
    const [isCollapsed, setIsCollapsed] = useState(false);

    return (
        <>
            {/* Mobile Navbar */}
            <Navbar
                maxWidth="full"
                className="lg:hidden glass-panel border-b border-divider"
                isMenuOpen={isMenuOpen}
                onMenuOpenChange={setIsMenuOpen}
                height="4rem"
            >
                <NavbarContent justify="start">
                    <NavbarMenuToggle
                        aria-label={isMenuOpen ? 'Close menu' : 'Open menu'}
                        className="lg:hidden"
                    />
                    <div className="flex items-center gap-2">
                        <div className="p-2 rounded-xl bg-gradient-to-br from-primary to-secondary">
                            <BarChart3 className="h-5 w-5 text-white" />
                        </div>
                        <span className="text-lg font-bold gradient-text">USSD Portal</span>
                    </div>
                </NavbarContent>

                <NavbarContent justify="end">
                    <ThemeSwitch />
                </NavbarContent>

                <NavbarMenu className="pt-8 px-4 bg-gradient-to-b from-background to-card">
                    {navItems.map((item) => {
                        const Icon = item.icon;
                        const isActive = pathname === item.href;

                        return (
                            <NavbarMenuItem key={item.href}>
                                <Link
                                    href={item.href}
                                    className={cn(
                                        'w-full flex items-center gap-3 px-4 py-4 rounded-xl transition-all duration-300',
                                        isActive
                                            ? 'bg-gradient-to-r from-primary/20 to-secondary/20 border-l-4 border-primary'
                                            : 'hover:bg-default-100/50'
                                    )}
                                    onClick={() => setIsMenuOpen(false)}
                                >
                                    <div className={cn(
                                        'p-2 rounded-lg transition-all',
                                        isActive
                                            ? 'bg-gradient-to-br from-primary to-secondary text-white'
                                            : 'bg-default-100 text-default-600'
                                    )}>
                                        <Icon className="h-5 w-5" />
                                    </div>
                                    <span className={cn(
                                        'font-medium',
                                        isActive ? 'text-primary' : 'text-foreground'
                                    )}>
                                        {item.label}
                                    </span>
                                    {item.badge && (
                                        <Badge
                                            size="sm"
                                            color={isActive ? "primary" : "default"}
                                            className="ml-auto"
                                        >
                                            {item.badge}
                                        </Badge>
                                    )}
                                </Link>
                            </NavbarMenuItem>
                        );
                    })}
                </NavbarMenu>
            </Navbar>

            {/* Desktop Sidebar */}
            <div className={cn(
                'hidden lg:flex flex-col fixed inset-y-0 z-50 transition-all duration-300',
                isCollapsed ? 'w-20' : 'w-72'
            )}>
                <div className="flex flex-col flex-grow glass-panel border-r border-divider pt-8 pb-4 overflow-y-auto">
                    {/* Header */}
                    <div className={cn(
                        'flex items-center px-6 mb-10 transition-all',
                        isCollapsed && 'justify-center px-4'
                    )}>
                        <div className="p-3 rounded-xl bg-gradient-to-br from-primary to-secondary shadow-glow">
                            <BarChart3 className="h-6 w-6 text-white" />
                        </div>
                        {!isCollapsed && (
                            <div className="ml-3 flex-1">
                                <span className="text-2xl font-bold gradient-text animate-float">
                                    USSD Portal
                                </span>
                                <p className="text-xs text-default-500 mt-1">Advanced Management System</p>
                            </div>
                        )}
                        <Button
                            isIconOnly
                            variant="light"
                            size="sm"
                            className={cn(
                                'ml-auto transition-all',
                                isCollapsed && 'ml-0'
                            )}
                            onPress={() => setIsCollapsed(!isCollapsed)}
                        >
                            {isCollapsed ? (
                                <ChevronRight className="h-4 w-4" />
                            ) : (
                                <ChevronLeft className="h-4 w-4" />
                            )}
                        </Button>
                    </div>

                    {/* Navigation */}
                    <nav className="flex-1 px-4 space-y-2">
                        {navItems.map((item) => {
                            const Icon = item.icon;
                            const isActive = pathname === item.href;

                            return (
                                <Link
                                    key={item.href}
                                    href={item.href}
                                    className={cn(
                                        'flex items-center px-4 py-3 text-sm font-medium rounded-xl transition-all duration-300 group',
                                        isActive
                                            ? 'bg-gradient-to-r from-primary/20 to-secondary/20 shadow-glow'
                                            : 'hover:bg-default-100/50'
                                    )}
                                >
                                    <div className={cn(
                                        'p-2 rounded-lg transition-all',
                                        isActive
                                            ? 'bg-gradient-to-br from-primary to-secondary text-white shadow-glow'
                                            : 'bg-default-100 text-default-600 group-hover:bg-primary/10 group-hover:text-primary'
                                    )}>
                                        <Icon className="h-5 w-5" />
                                    </div>
                                    {!isCollapsed && (
                                        <>
                                            <span className={cn(
                                                'ml-3 transition-all',
                                                isActive ? 'text-primary font-semibold' : 'text-foreground'
                                            )}>
                                                {item.label}
                                            </span>
                                            {item.badge && (
                                                <Badge
                                                    size="sm"
                                                    color={isActive ? "primary" : "default"}
                                                    className="ml-auto"
                                                >
                                                    {item.badge}
                                                </Badge>
                                            )}
                                        </>
                                    )}
                                </Link>
                            );
                        })}
                    </nav>

                    <Divider className="my-6" />

                    {/* Footer */}
                    <div className="px-4 space-y-3">
                        {!isCollapsed && (
                            <div className="px-4 py-3 rounded-xl bg-gradient-to-r from-primary/10 to-secondary/10">
                                <div className="flex items-center gap-3">
                                    <div className="p-2 rounded-lg bg-gradient-to-br from-primary to-secondary">
                                        <Zap className="h-4 w-4 text-white" />
                                    </div>
                                    <div className="flex-1">
                                        <p className="text-sm font-medium">Pro Plan</p>
                                        <p className="text-xs text-default-500">Upgrade for more features</p>
                                    </div>
                                </div>
                            </div>
                        )}

                        <div className="flex items-center gap-2">
                            <ThemeSwitch />
                            {!isCollapsed && <span className="text-sm text-default-500">Theme</span>}
                        </div>

                        <Button
                            fullWidth
                            variant="flat"
                            className="justify-start text-foreground"
                            startContent={<Settings className="h-5 w-5" />}
                            isIconOnly={isCollapsed}
                        >
                            {!isCollapsed && "Settings"}
                        </Button>

                        <Button
                            fullWidth
                            variant="flat"
                            color="danger"
                            className="justify-start"
                            startContent={<LogOut className="h-5 w-5" />}
                            isIconOnly={isCollapsed}
                        >
                            {!isCollapsed && "Logout"}
                        </Button>
                    </div>
                </div>
            </div>
        </>
    );
}